"use client";

import React, { useState } from "react";
import { useTonWallet } from "@/hooks/useTonWallet";
import { useGame, useGameActions } from "@/context/GameContext";
import { ErrorMessage } from "@/components/ui/ErrorMessage";
import { GameChoice, GameState } from "@/types/game";

export default function GameInterface() {
  const [gameState, setGameState] = useState<GameState>("initial");
  const [stake, setStake] = useState(1);
  const [gameId, setGameId] = useState("");
  const [selectedChoice, setSelectedChoice] = useState<GameChoice | null>(null);
  
  const wallet = useTonWallet();
  const { state } = useGame();
  const { createGame, joinGame, submitChoice, setError, clearError } = useGameActions();

  const handleCreateGame = async () => {
    try {
      if (!wallet.isConnected) {
        setGameState("connecting");
        await wallet.connect();
      }
      setGameState("creating");
      const newGameId = await wallet.createGame(stake);
      createGame(stake, wallet.address!);
      setGameId(newGameId);
      setGameState("commit");
    } catch (error) {
      setError({
        code: "CREATE_GAME_ERROR",
        message: "Failed to create game. Please try again.",
      });
      setGameState("initial");
    }
  };

  const handleJoinGame = async (joinGameId: string) => {
    try {
      if (!wallet.isConnected) {
        setGameState("connecting");
        await wallet.connect();
      }
      await wallet.joinGame(joinGameId, stake);
      joinGame(joinGameId, wallet.address!);
      setGameId(joinGameId);
      setGameState("commit");
    } catch (error) {
      setError({
        code: "JOIN_GAME_ERROR",
        message: "Failed to join game. Please check the game ID and try again.",
      });
      setGameState("initial");
    }
  };

  const handleChoiceSelection = async (choice: GameChoice) => {
    try {
      setSelectedChoice(choice);
      submitChoice(wallet.address!, choice);
      setGameState("reveal");
    } catch (error) {
      setError({
        code: "CHOICE_ERROR",
        message: "Failed to submit your choice. Please try again.",
      });
    }
  };

  const renderGameState = () => {
    switch (gameState) {
      case "initial":
        return (
          <div className="space-y-6">
            {wallet.isConnected ? (
              <div className="bg-gray-100 p-4 rounded-md mb-4">
                <p className="text-sm text-gray-600">Connected Wallet</p>
                <p className="font-mono text-xs truncate">{wallet.address}</p>
                <p className="text-sm font-semibold">{wallet.balance} TON</p>
              </div>
            ) : (
              <button
                onClick={wallet.connect}
                className="w-full bg-gray-100 text-black py-3 rounded-md font-semibold hover:bg-gray-200 transition mb-4"
              >
                Connect Wallet
              </button>
            )}
            <div>
              <label htmlFor="stake" className="block text-lg font-semibold mb-2">
                Select your stake (TON)
              </label>
              <input
                id="stake"
                type="number"
                min={1}
                value={stake}
                onChange={(e) => setStake(Number(e.target.value))}
                className="w-full border border-gray-300 rounded-md px-3 py-2 text-black"
              />
            </div>
            <div className="flex space-x-4">
              <button
                type="button"
                className="flex-1 bg-black text-white py-3 rounded-md font-semibold hover:bg-gray-800 transition"
                onClick={handleCreateGame}
              >
                Create Game
              </button>
              <button
                type="button"
                className="flex-1 border border-black text-black py-3 rounded-md font-semibold hover:bg-gray-100 transition"
                onClick={() => setGameState("joining")}
              >
                Join Game
              </button>
            </div>
          </div>
        );

      case "connecting":
        return (
          <div className="text-center space-y-4">
            <h2 className="text-xl font-semibold">Connecting Wallet</h2>
            <div className="animate-pulse">Please confirm in your TON wallet...</div>
            <button
              onClick={() => setGameState("initial")}
              className="text-sm text-gray-600 hover:text-black transition"
            >
              Cancel
            </button>
          </div>
        );

      case "creating":
        return (
          <div className="text-center space-y-4">
            <h2 className="text-xl font-semibold">Creating New Game</h2>
            <p>Stake: {stake} TON</p>
            <div className="animate-pulse">Connecting to wallet...</div>
            <button
              onClick={() => setGameState("initial")}
              className="text-sm text-gray-600 hover:text-black transition"
            >
              Cancel
            </button>
          </div>
        );

      case "joining":
        return (
          <div className="text-center space-y-4">
            <h2 className="text-xl font-semibold">Join Game</h2>
            <input
              type="text"
              placeholder="Enter game link or ID"
              value={gameId}
              onChange={(e) => setGameId(e.target.value)}
              className="w-full border border-gray-300 rounded-md px-3 py-2 text-black"
            />
            <button
              onClick={() => handleJoinGame(gameId)}
              className="w-full bg-black text-white py-3 rounded-md font-semibold hover:bg-gray-800 transition"
            >
              Join
            </button>
            <button
              onClick={() => setGameState("initial")}
              className="text-sm text-gray-600 hover:text-black transition"
            >
              Cancel
            </button>
          </div>
        );

      case "commit":
        return (
          <div className="text-center space-y-4">
            <h2 className="text-xl font-semibold">Make Your Choice</h2>
            <div className="grid grid-cols-3 gap-4">
              {["Rock", "Paper", "Scissors"].map((choice) => (
                <button
                  key={choice}
                  onClick={() => handleChoiceSelection(choice as GameChoice)}
                  className={`p-4 border ${
                    selectedChoice === choice
                      ? "border-black bg-black text-white"
                      : "border-gray-300 hover:bg-gray-100"
                  } rounded-md transition`}
                >
                  {choice}
                </button>
              ))}
            </div>
          </div>
        );

      case "reveal":
        return (
          <div className="text-center space-y-4">
            <h2 className="text-xl font-semibold">Waiting for Opponent</h2>
            <div className="animate-pulse">
              Your choice has been submitted...
            </div>
          </div>
        );

      case "result":
        return (
          <div className="text-center space-y-4">
            <h2 className="text-xl font-semibold">Game Result</h2>
            <div className="p-4 bg-gray-100 rounded-md">
              <p className="text-2xl font-bold">You Won!</p>
              <p className="text-gray-600">Prize: {stake * 2} TON</p>
            </div>
            <button
              onClick={() => setGameState("initial")}
              className="w-full bg-black text-white py-3 rounded-md font-semibold hover:bg-gray-800 transition"
            >
              Play Again
            </button>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="w-full max-w-md mx-auto">
      {renderGameState()}
    </div>
  );
}
